@import url("https://fonts.googleapis.com/css?family=Roboto:400,500,700");
@import url("https://fonts.googleapis.com/css?family=Karla:400,700");
:root {
  --tw-body-font-family: theme(fontFamily.base);
  --tw-heading-font-family: theme(fontFamily.heading);
  --tw-body-bg: theme(backgroundColor.gray.100);
  --tw-body-color: theme(colors.gray.800);
  --tw-sidenav-width: 240px;
  --tw-sidenav-width-md: 160px;
  --tw-sidenav-width-sm: 70px;
  --tw-topbar-height: 70px;
  --tw-menu-item-padding-x: 10px;
  --tw-menu-item-gap: 5px;
  --tw-menu-link-padding-x: 15px;
  --tw-menu-link-padding-y: 10px;
  --tw-menu-link-item-gap: 10px;
  --tw-menu-item-icon-size: 18px;
  --tw-menu-item-font-size: 16px;
}

:root:is([data-mode=dark]) {
  --tw-body-bg: theme(backgroundColor.gray.900);
  --tw-body-color: theme(colors.gray.100);
}

html[data-menu-color=light] {
  --tw-menu-bg: white;
  --tw-menu-item-color: rgb(108, 117, 125);
  --tw-menu-item-active-color: theme(backgroundColor.primary);
  --tw-menu-item-hover-color: theme(backgroundColor.primary);
}

html[data-menu-color=dark],
html[data-mode=dark][data-menu-color=light] {
  --tw-menu-bg: theme(backgroundColor.gray.800);
  --tw-menu-item-color: rgb(144, 151, 167);
  --tw-menu-item-active-color: rgb(200, 205, 220);
  --tw-menu-item-hover-color: rgb(200, 205, 220);
}

html[data-menu-color=brand] {
  --tw-menu-bg: theme(backgroundColor.primary);
  --tw-menu-item-color: theme(colors.gray.100);
  --tw-menu-item-active-color: theme(colors.white);
  --tw-menu-item-hover-color: theme(colors.white);
}

html[data-menu-color=gradient] {
  --tw-menu-bg: linear-gradient(270deg,#4095d8, #5b69bc);
  --tw-menu-item-color: rgb(206, 220, 228);
  --tw-menu-item-active-color: rgb(255, 255, 255);
  --tw-menu-item-hover-color: rgb(255, 255, 255);
}

html[data-topbar-color=light] {
  --tw-topbar-bg: theme(backgroundColor.gray.100);
  --tw-topbar-item-color: rgb(108, 117, 125);
  --tw-topbar-item-active-color: theme(backgroundColor.primary);
  --tw-topbar-item-hover-color: theme(backgroundColor.primary);
}

html[data-topbar-color=dark],
html[data-mode=dark][data-topbar-color=light] {
  --tw-topbar-bg: theme(backgroundColor.gray.900);
  --tw-topbar-item-color: rgb(131, 145, 162);
  --tw-topbar-item-active-color: rgb(188, 206, 228);
  --tw-topbar-item-hover-color: rgb(188, 206, 228);
}

@tailwind base;
@tailwind components;
@tailwind utilities;
.app-menu {
  width: var(--tw-sidenav-width);
  min-width: var(--tw-sidenav-width);
  background: var(--tw-menu-bg);
  @apply sticky top-0 h-screen z-40 shadow transition-all duration-300;
}
.app-menu [data-simplebar] {
  height: calc(100% - var(--tw-topbar-height));
}

.page-content {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-block-size: 100vh;
}

.logo-box {
  height: var(--tw-topbar-height);
  color: var(--tw-menu-item-color);
  @apply sticky top-0 flex items-center justify-center transition-all duration-300;
}
.logo-box .logo-lg {
  height: 16px;
}
.logo-box .logo-sm {
  height: 22px;
}
.logo-box .logo-sm {
  display: none;
}
.logo-box .logo-light {
  display: none;
}
.logo-box .logo-dark {
  display: block;
}

html[data-mode=dark] .app-menu .logo-box .logo-light,
html[data-menu-color=brand] .app-menu .logo-box .logo-light,
html[data-menu-color=dark] .app-menu .logo-box .logo-light,
html[data-menu-color=gradient] .app-menu .logo-box .logo-light {
  display: block;
}
html[data-mode=dark] .app-menu .logo-box .logo-dark,
html[data-menu-color=brand] .app-menu .logo-box .logo-dark,
html[data-menu-color=dark] .app-menu .logo-box .logo-dark,
html[data-menu-color=gradient] .app-menu .logo-box .logo-dark {
  display: none;
}

.menu {
  display: flex;
  flex-direction: column;
  gap: var(--tw-menu-item-gap);
  padding: var(--tw-menu-item-gap) 0;
}
.menu .menu-title {
  color: var(--tw-menu-item-color);
  font-family: var(--tw-heading-font-family);
  font-size: calc(var(--tw-menu-item-font-size) * 0.8);
  padding: var(--tw-menu-link-padding-y) calc(var(--tw-menu-link-padding-x) + var(--tw-menu-item-padding-x));
  @apply font-semibold uppercase opacity-75 cursor-default tracking-wider whitespace-nowrap;
}
.menu > .menu-item {
  padding: 0 var(--tw-menu-item-padding-x);
}
.menu .menu-item .menu-link {
  color: var(--tw-menu-item-color);
  gap: var(--tw-menu-link-item-gap);
  padding: var(--tw-menu-link-padding-y) var(--tw-menu-link-padding-x);
  font-family: var(--tw-heading-font-family);
  @apply relative flex items-center transition-all whitespace-nowrap rounded-lg bg-transparent;
}
.menu .menu-item .menu-link > .menu-icon {
  width: var(--tw-menu-item-icon-size);
  height: var(--tw-menu-item-icon-size);
  font-size: var(--tw-menu-item-icon-size);
  line-height: var(--tw-menu-item-icon-size);
  @apply transition-all;
}
.menu .menu-item .menu-link > .menu-icon svg {
  width: var(--tw-menu-item-icon-size);
  height: var(--tw-menu-item-icon-size);
  stroke: inherit;
}
.menu .menu-item .menu-link > .menu-text {
  font-size: var(--tw-menu-item-font-size);
  line-height: var(--tw-menu-item-font-size);
  @apply whitespace-nowrap transition-all;
}
.menu .menu-item .menu-link > .menu-arrow {
  font-size: var(--tw-menu-item-font-size);
  line-height: var(--tw-menu-item-font-size);
  font-family: "Material Design Icons";
  @apply transition-all ms-auto rtl:rotate-180;
}
.menu .menu-item .menu-link > .menu-arrow::before {
  content: "\f0142";
}
.menu .menu-item .menu-link > .badge {
  @apply flex justify-center items-center h-5 px-2 ms-auto font-semibold text-white text-center whitespace-nowrap align-baseline;
  font-size: calc(var(--tw-menu-item-font-size) * 0.8);
}
.menu .menu-item .menu-link:is(.open) > .menu-arrow {
  transform: rotate(90deg);
}
.menu .menu-item .menu-link:is(.open, .active, :active, :focus) {
  color: var(--tw-menu-item-active-color);
}
.menu .menu-item .menu-link:is(:hover) {
  color: var(--tw-menu-item-hover-color);
}
.menu .sub-menu {
  @apply w-full overflow-hidden transition-all duration-300;
  padding-inline-start: calc(var(--tw-menu-item-icon-size) + var(--tw-menu-link-item-gap));
}
.menu .sub-menu .sub-menu {
  padding-inline-start: calc(var(--tw-menu-item-icon-size) + var(--tw-menu-link-item-gap));
}

.user-box {
  color: var(--tw-menu-item-color);
}

html[data-sidenav-view=condensed]:not([data-layout=horizontal]) .app-menu {
  width: var(--tw-sidenav-width-sm);
  min-width: var(--tw-sidenav-width-sm);
  min-height: 1500px;
  height: unset;
  position: static;
}
html[data-sidenav-view=condensed]:not([data-layout=horizontal]) .app-menu .simplebar-mask,
html[data-sidenav-view=condensed]:not([data-layout=horizontal]) .app-menu .simplebar-content-wrapper {
  overflow: visible !important;
}
html[data-sidenav-view=condensed]:not([data-layout=horizontal]) .app-menu .logo-box {
  background: var(--tw-menu-bg);
  @apply z-40;
}
html[data-sidenav-view=condensed]:not([data-layout=horizontal]) .app-menu .logo-box .logo-lg {
  display: none;
}
html[data-sidenav-view=condensed]:not([data-layout=horizontal]) .app-menu .logo-box .logo-sm {
  display: flex;
}
html[data-sidenav-view=condensed]:not([data-layout=horizontal]) .app-menu .user-box {
  display: none;
}
html[data-sidenav-view=condensed]:not([data-layout=horizontal]) .app-menu .menu {
  padding: 0;
}
html[data-sidenav-view=condensed]:not([data-layout=horizontal]) .app-menu .menu .menu-title {
  display: none;
}
html[data-sidenav-view=condensed]:not([data-layout=horizontal]) .app-menu .menu .menu-item {
  position: relative;
}
html[data-sidenav-view=condensed]:not([data-layout=horizontal]) .app-menu .menu .menu-item > .menu-link {
  transition: none;
}
html[data-sidenav-view=condensed]:not([data-layout=horizontal]) .app-menu .menu .menu-item > .menu-link > .menu-text,
html[data-sidenav-view=condensed]:not([data-layout=horizontal]) .app-menu .menu .menu-item > .menu-link > .menu-arrow,
html[data-sidenav-view=condensed]:not([data-layout=horizontal]) .app-menu .menu .menu-item > .menu-link > .badge {
  transition: none;
  display: none;
}
html[data-sidenav-view=condensed]:not([data-layout=horizontal]) .app-menu .menu .menu-item .sub-menu {
  display: none;
  height: inherit !important;
}
html[data-sidenav-view=condensed]:not([data-layout=horizontal]) .app-menu .menu .menu-item:hover > .sub-menu {
  display: block;
  inset-inline-start: 100%;
  overflow: visible;
  position: absolute;
  height: inherit !important;
  transition: none;
  padding-inline-start: 0;
  background: var(--tw-menu-bg);
  width: calc(var(--tw-sidenav-width) - var(--tw-sidenav-width-sm));
  @apply border border-solid border-gray-500/20;
}
html[data-sidenav-view=condensed]:not([data-layout=horizontal]) .app-menu .menu .menu-item:hover > .sub-menu > .menu-item > .menu-link {
  z-index: 5;
}
html[data-sidenav-view=condensed]:not([data-layout=horizontal]) .app-menu .menu .menu-item:hover > .sub-menu > .menu-item > .menu-link > .menu-arrow,
html[data-sidenav-view=condensed]:not([data-layout=horizontal]) .app-menu .menu .menu-item:hover > .sub-menu > .menu-item > .menu-link > .menu-text {
  display: block;
}
html[data-sidenav-view=condensed]:not([data-layout=horizontal]) .app-menu .menu .menu-item:hover > .sub-menu > .menu-item .sub-menu {
  top: 0;
}
html[data-sidenav-view=condensed]:not([data-layout=horizontal]) .app-menu .menu > .menu-item > .menu-link {
  @apply flex text-center justify-center;
}
html[data-sidenav-view=condensed]:not([data-layout=horizontal]) .app-menu .menu > .menu-item:hover > .menu-link {
  color: var(--tw-menu-item-hover-color);
  background-color: var(--tw-menu-item-hover-bg);
}
html[data-sidenav-view=condensed]:not([data-layout=horizontal]) .app-menu .menu > .menu-item:hover > .menu-link::after {
  content: "";
  inset: 0;
  position: absolute;
  width: var(--tw-sidenav-width);
}
html[data-sidenav-view=condensed]:not([data-layout=horizontal]) .app-menu .menu > .menu-item:hover > .menu-link > .menu-text {
  background: var(--tw-menu-bg);
  line-height: var(--tw-menu-item-icon-size);
  width: calc(var(--tw-sidenav-width) - var(--tw-sidenav-width-sm));
  padding: var(--tw-menu-link-padding-y) var(--tw-menu-link-padding-x);
  inset-inline-start: calc(var(--tw-sidenav-width-sm) - var(--tw-menu-item-padding-x));
  @apply absolute flex items-center border border-solid border-gray-500/20;
}
html[data-sidenav-view=condensed]:not([data-layout=horizontal]) .app-menu .menu > .menu-item > .menu-link[data-fc-type=collapse] > .menu-text {
  @apply rounded-t-md;
}
html[data-sidenav-view=condensed]:not([data-layout=horizontal]) .app-menu .menu > .menu-item > .menu-link:not([data-fc-type=collapse]) > .menu-text {
  @apply rounded-md;
}
html[data-sidenav-view=condensed]:not([data-layout=horizontal]) .app-menu .menu > .menu-item > .sub-menu {
  @apply rounded-b-md;
}
html[data-sidenav-view=condensed]:not([data-layout=horizontal]) .app-menu .menu > .menu-item > .sub-menu .sub-menu {
  @apply rounded-md;
}
html[data-sidenav-view=condensed]:not([data-layout=horizontal]) .app-menu .menu > .menu-item > .menu-link > .menu-text,
html[data-sidenav-view=condensed]:not([data-layout=horizontal]) .app-menu .menu > .menu-item .sub-menu {
  @apply ms-1.5 shadow-lg;
}

html[data-sidenav-view=compact]:not([data-layout=horizontal]) .app-menu {
  width: var(--tw-sidenav-width-md);
  min-width: var(--tw-sidenav-width-md);
}
html[data-sidenav-view=compact]:not([data-layout=horizontal]) .app-menu .menu .menu-title {
  @apply text-center underline decoration-dashed;
}
html[data-sidenav-view=compact]:not([data-layout=horizontal]) .app-menu .menu .menu-link {
  @apply flex-col text-center;
}
html[data-sidenav-view=compact]:not([data-layout=horizontal]) .app-menu .menu .menu-link .badge,
html[data-sidenav-view=compact]:not([data-layout=horizontal]) .app-menu .menu .menu-link .menu-arrow {
  @apply hidden;
}
html[data-sidenav-view=compact]:not([data-layout=horizontal]) .app-menu .menu .sub-menu {
  @apply ps-0;
}
html[data-sidenav-view=compact]:not([data-layout=horizontal]) .app-menu .menu .sub-menu .menu-link .menu-arrow {
  @apply ms-0 block;
}
html[data-sidenav-view=compact]:not([data-layout=horizontal]) .app-menu .menu .sub-menu .menu-text {
  @apply ps-0;
}
html[data-sidenav-view=compact]:not([data-layout=horizontal]) .app-menu .help-box {
  @apply hidden;
}

html[data-sidenav-view=mobile] .app-menu {
  margin-inline-start: calc(var(--tw-sidenav-width) * -1);
  @apply fixed opacity-0;
}
html[data-sidenav-view=mobile].sidenav-enable .app-menu {
  margin-inline-start: 0;
  @apply opacity-100 z-50;
}

html[data-sidenav-view=hidden] .app-menu {
  @apply opacity-0;
  margin-inline-start: calc(var(--tw-sidenav-width) * -1);
}

html[data-layout-width=boxed] {
  background: var(--tw-body-bg);
}
html[data-layout-width=boxed] body {
  @apply w-full mx-auto max-w-[1300px] shadow-md xl:border-x xl:border-gray-300/10;
}

html[data-layout-position=scrollable] .wrapper {
  overflow: hidden;
}
html[data-layout-position=scrollable] .wrapper .app-menu {
  height: auto;
}
html[data-layout-position=scrollable][data-sidenav-view=mobile] .app-menu {
  height: 100vh;
}

@screen xl {
  [data-layout=horizontal] .wrapper {
    flex-direction: column;
  }
  [data-layout=horizontal] .app-menu {
    width: 100%;
    min-width: 100%;
    top: var(--tw-topbar-height);
    display: flex;
    align-items: center;
    @apply h-14 z-20;
  }
  [data-layout=horizontal] .app-header {
    margin-top: calc(var(--tw-topbar-height) * -1);
  }
  [data-layout=horizontal] .app-header .logo-box {
    @apply flex pe-6;
  }
  [data-layout=horizontal] .app-header #button-toggle-menu {
    @apply hidden;
  }
  [data-layout=horizontal] footer > *,
  [data-layout=horizontal] .app-header > *,
  [data-layout=horizontal] .app-menu > *,
  [data-layout=horizontal] main {
    @apply container;
  }
  [data-layout=horizontal] .page-content {
    min-block-size: calc(100vh - 56px);
  }
  [data-layout=horizontal] .page-content main {
    margin-top: 70px;
  }
  [data-layout=horizontal] .menu {
    flex-direction: row;
    padding: 0;
    gap: 0;
  }
  [data-layout=horizontal] .menu .menu-item {
    position: relative;
  }
  [data-layout=horizontal] .menu .menu-item > .menu-link::after {
    content: "";
    inset: 0;
    position: absolute;
    width: 100%;
    height: 50px;
  }
  [data-layout=horizontal] .menu .menu-item .sub-menu {
    display: none;
    height: inherit !important;
    @apply mt-2;
  }
  [data-layout=horizontal] .menu .menu-item:hover > .sub-menu {
    display: block;
    overflow: visible;
    position: absolute;
    height: inherit !important;
    transition: none;
    padding-inline-start: 0;
    background: var(--tw-menu-bg);
    width: 12rem;
    @apply shadow rounded;
  }
  [data-layout=horizontal] .menu .menu-item:hover > .sub-menu > .menu-item .sub-menu {
    top: 0;
    inset-inline-start: 100%;
  }
  [data-layout=horizontal] #leftSidebarSize {
    @apply hidden;
  }
  [data-layout=horizontal][data-layout-position=scrollable] .wrapper .app-menu {
    @apply h-14;
  }
}
html[data-sidenav-view=mobile][data-layout=horizontal] .app-menu {
  overflow-y: auto;
}
html[data-sidenav-view=mobile][data-layout=horizontal] .app-menu .menu > .menu-item > .sub-menu {
  padding-inline-start: 10px;
}

.app-header {
  @apply sticky top-0;
  z-index: 35;
  background: var(--tw-topbar-bg);
  height: var(--tw-topbar-height);
}
.app-header .nav-link {
  color: var(--tw-topbar-item-color);
  height: var(--tw-topbar-height);
  @apply rounded-full transition-all ease-in-out;
}
.app-header .nav-link:hover {
  color: var(--tw-topbar-item-hover-color);
}
.app-header .nav-link:is(.open, .show, .active, :active, :focus) {
  color: var(--tw-topbar-item-active-color);
}
.app-header .logo-box {
  @apply xl:hidden;
}
.app-header .logo-box .logo-lg {
  @apply md:block hidden;
}
.app-header .logo-box .logo-sm {
  @apply md:hidden block;
}
.app-header .form-input,
.app-header .form-input::placeholder,
.app-header .mdi-magnify,
.app-header .page-title {
  @apply text-slate-900;
}

html[data-mode=dark] .app-header .logo-box .logo-light,
html[data-topbar-color=dark] .app-header .logo-box .logo-light {
  display: block;
}
html[data-mode=dark] .app-header .logo-box .logo-dark,
html[data-topbar-color=dark] .app-header .logo-box .logo-dark {
  display: none;
}
html[data-mode=dark] .app-header .form-input,
html[data-mode=dark] .app-header .form-input::placeholder,
html[data-mode=dark] .app-header .mdi-magnify,
html[data-mode=dark] .app-header .page-title,
html[data-topbar-color=dark] .app-header .form-input,
html[data-topbar-color=dark] .app-header .form-input::placeholder,
html[data-topbar-color=dark] .app-header .mdi-magnify,
html[data-topbar-color=dark] .app-header .page-title {
  @apply text-slate-200;
}

html {
  position: relative;
  scroll-behavior: smooth;
}

body {
  overflow-x: hidden;
  color: var(--tw-body-color);
  background-color: var(--tw-body-bg);
  font-family: var(--tw-body-font-family);
  -webkit-tap-highlight-color: transparent;
  @apply antialiased font-normal text-sm leading-normal;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--tw-heading-font-family);
  @apply font-semibold;
}

code {
  @apply text-[#ff8acc];
}

:root:is([data-mode=dark]) {
  color-scheme: dark;
}

canvas {
  display: inherit;
}

@layer components {
  .btn {
    @apply inline-flex items-center justify-center text-center rounded-sm border border-transparent px-4 py-2 text-sm transition-all font-medium;
  }
  .btn.btn-lg {
    @apply px-5 py-2.5 text-base;
  }
  .btn.btn-sm {
    @apply px-3.5 py-1.5 text-xs;
  }
}
@layer components {
  .card {
    @apply bg-white dark:bg-gray-800 shadow-sm rounded h-fit;
  }
  .card-title {
    @apply text-base font-semibold text-slate-800 dark:text-slate-200;
  }
}
@layer components {
  label {
    @apply block text-sm font-medium cursor-pointer text-gray-800 dark:text-gray-400;
  }
  .form-input,
  .form-select {
    @apply font-normal rounded block w-full border-gray-200 text-sm focus:border-gray-300 
        focus:ring-0 bg-transparent dark:border-gray-700 dark:text-gray-400;
  }
  .form-input-sm,
  .form-select-sm {
    @apply py-1 ps-2 pe-8 text-xs;
  }
  .form-textarea {
    @apply font-normal rounded block w-full border-gray-200 text-sm focus:border-gray-300 
        focus:ring-0 bg-transparent dark:border-gray-700 dark:text-gray-400;
  }
  .form-range {
    @apply w-full h-2 bg-gray-200 rounded-lg appearance-none cursor-pointer dark:bg-gray-700;
  }
  [type=file] {
    @apply p-0 file:bg-transparent file:border-0 file:bg-gray-100 file:me-4 file:py-2 file:px-4 dark:file:bg-gray-700 dark:file:text-gray-400;
  }
  .form-checkbox,
  .form-radio {
    @apply bg-inherit cursor-pointer border-gray-300 dark:border-gray-600 dark:checked:border-transparent focus:ring-0 focus:ring-offset-0;
  }
  .form-switch {
    @apply w-9 h-5 flex items-center appearance-none bg-transparent border-2 border-transparent rounded-full ring-1 ring-gray-300 focus:ring-1 focus:ring-gray-300 checked:focus:ring-transparent checked:ring-transparent dark:ring-gray-600 dark:checked:ring-transparent focus:ring-offset-0 cursor-pointer transition-colors ease-in-out duration-200 checked:bg-none;
  }
  .form-switch::before {
    @apply content-[""] inline-block w-4 h-4 bg-gray-200 rounded-full translate-x-0 transform transition ease-in-out duration-200 dark:bg-gray-600 checked:bg-white checked:dark:bg-white checked:translate-x-full rtl:checked:-translate-x-full;
  }
  .form-switch.form-switch-lg {
    @apply w-11 h-6;
  }
  .form-switch.form-switch-lg::before {
    @apply w-5 h-5;
  }
  .form-switch.form-switch-sm {
    @apply w-7 h-4;
  }
  .form-switch.form-switch-sm::before {
    @apply w-3 h-3;
  }
  .form-switch.square {
    @apply rounded-none before:rounded-none;
